<template>
    <div class="row text-center flex flex-center q-mt-xl q-mx-lg">
        <div class="col-12">
            <div class="row q-mb-lg">
                <div class="col-12">
                    <div class="text-h4 text-center text-bold text">
                        ?
                    </div>
                </div>
            </div>

            <div class="row q-ma-xl">
                <div class="col-12">
                    <q-card class="bg-mainlight2">
                        <q-card-section class="text text-h6 text-center">
                               <br>
                            <br>
                              0 <br>
                              1 <br>
                              2 <br>
                               3 <br>
                        </q-card-section>
                    </q-card>
                </div>
            </div>

            <div class="row q-ma-xl">
                <div class="col-12">
                    <q-card class="bg-mainlight">
                        <q-card-section class="text text-h6 text-left" v-for="(item, index) in questionnaireSet" :key="index">
                            {{item.question}}
                            <q-item>
                                <q-item-section side>
                                    <q-icon name="sentiment_very_satisfied"  size="lg" color="secondary" />
                                </q-item-section>

                                <q-item-section>
                                    <q-slider v-model="item.rating" marker-labels :min="0" :max="3"/>
                                </q-item-section>

                                

                                <q-item-section side>
                                    <q-icon name="sentiment_dissatisfied" size="lg" color="secondary" />
                                </q-item-section>
                            </q-item>
                        </q-card-section>
                    </q-card>
                </div>
            </div>

            <div class="row">
                <div class="col-12">
                    <q-btn size="xl" class="bg-maindark text-white" @click="handleSubmitData">
                        
                    </q-btn>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { onMounted, ref, watchEffect, watch } from 'vue'
import { useRouter } from 'vue-router'
import { useQuasar } from 'quasar'
import FirestoreHandle from '../composables/FirestoreHandle'
import AuthenComposable from '../composables/AuthenComposable'

export default {
    setup () {
        const questionnaireSet = [
            {
                id: 1,
                question: '1. ',
                rating: 0
            },
            {
                id: 2,
                question: '2. ',
                rating: 0
            },
            {
                id: 3,
                question: '3. ',
                rating: 0
            },
            {
                id: 4,
                question: '4.  (  )',
                rating: 0
            },
            {
                id: 5,
                question: '5.  ',
                rating: 0
            },
            {
                id: 6,
                question: '6.  ',
                rating: 0
            },
            {
                id: 7,
                question: '7.  ( )',
                rating: 0
            },
            {
                id: 8,
                question: '8. ',
                rating: 0
            },
            {
                id: 9,
                question: '9. ',
                rating: 0
            },
            {
                id: 10,
                question: '10. ',
                rating: 0
            },
            {
                id: 11,
                question: '11.',
                rating: 0
            },
            {
                id: 12,
                question: '12. ',
                rating: 0
            },
            {
                id: 13,
                question: '13. ',
                rating: 0
            },
            {
                id: 14,
                question: '14. ',
                rating: 0
            },
            {
                id: 15,
                question: '15. ',
                rating: 0
            },
            {
                id: 16,
                question: '16. ',
                rating: 0
            },
            {
                id: 17,
                question: '17. ',
                rating: 0
            },
            {
                id: 18,
                question: '18. ',
                rating: 0
            },
            {
                id: 19,
                question: '19.  (  )',
                rating: 0
            },
            {
                id: 20,
                question: '20.  ',
                rating: 0
            },
            {
                id: 21,
                question: '21. ',
                rating: 0
            },
        ]

        const { setDocData, updateDocData, getDocData, getRoundData, DocResult } = FirestoreHandle()
        const { currentUser } = AuthenComposable()

        const currentRound = ref(null)

        const router = useRouter()

        const $q = useQuasar()

        const alert = () => {
            $q.dialog({
                title: '!!',
                message: ''
            }).onOk(() => {
                // console.log('OK')
            }).onCancel(() => {
                // console.log('Cancel')
            }).onDismiss(() => {
                // console.log('I am triggered on both OK and Cancel')
            })
        }

        onMounted(()=>{
            alert()
        })

        // watchEffect(()=>{
        //     if(currentUser.value)
        //     {
        //         getRoundData(currentUser.value.email).then(()=>{
        //             currentRound.value = DocResult.value
        //         })
        //     }

        // })

        watch(currentUser, (newValue, oldValue) => {
            if(newValue)
            {
                getRoundData(currentUser.value.email).then(()=>{
                    currentRound.value = DocResult.value
                })
            }
        })

        const handleSubmitData = () => {
            const depressionIds = [3, 5, 10, 13, 16, 17, 21]
            const depressionScore = questionnaireSet
            .filter(question => depressionIds.includes(question.id))
            .reduce((sum, question) => sum + question.rating, 0)

            const anxietyIds = [2, 4, 7, 9, 15, 19, 20]
            const anxietyScore = questionnaireSet
            .filter(question => anxietyIds.includes(question.id))
            .reduce((sum, question) => sum + question.rating, 0)

            const stressIds = [1, 6, 8, 11, 12, 14, 18]
            const stressScore = questionnaireSet
            .filter(question => stressIds.includes(question.id))
            .reduce((sum, question) => sum + question.rating, 0)

            const data = {
                EmotionalAssessment: true,
                AccessDate: new Date().toLocaleDateString(),
                OverallScore: {
                    DepressionScore: depressionScore,
                    AnxietyScore: anxietyScore,
                    StressScore: stressScore
                },
                Items: {
                    Depression: questionnaireSet.filter(question => depressionIds.includes(question.id)),
                    Anxiety: questionnaireSet.filter(question => anxietyIds.includes(question.id)),
                    Stress: questionnaireSet.filter(question => stressIds.includes(question.id)),
                }
            }
            // console.log(data)
            updateDocData(`/users/${currentUser.value.email}/${String(currentRound.value)}/Emotional Data`, data).then(()=>{
                router.push('/emotionalResult')
            })
        }

        return {questionnaireSet, handleSubmitData, alert}
    }
}
</script>

